<template>
  <v-app-bar
    v-scroll="onScroll"
    :color="!isScrolling ? 'transparent' : 'white'"
    fixed
    flat
  >
    <v-slide-x-transition>
      <v-img
        v-if="showLogo"
        :src="require('@/assets/title.svg')"
        class="shrink"
        contain
        height="50"
      />
    </v-slide-x-transition>
    <v-spacer />
    <v-toolbar-items class="hidden-sm-and-down">
      <social-media />
    </v-toolbar-items>
    <v-app-bar-nav-icon
      class="hidden-md-and-up"
      @click.stop="drawer = !drawer"
    />
    <v-navigation-drawer
      v-model="drawer"
      app
      right
      absolute
      temporary
    >
      <v-list
        nav
        dense
      >
        <v-list-item-group
          v-model="group"
          active-class="deep-purple--text text--accent-4"
        >
          <v-list-item>
            <v-btn
              class="ma-2 accent"
              href="https://www.instagram.com/earthaid.solutions/"
              target="_blank"
              block
            >
              Instagram
              <v-icon
                dark
                right
                small
              >
                mdi-instagram
              </v-icon>
            </v-btn>
          </v-list-item>

          <v-list-item>
            <v-btn
              class="ma-2 accent"
              href="https://www.facebook.com/pages/category/Nonprofit-Organization/EarthAid-Solutions-eV-1677491692296731/"
              target="_blank"
              block
            >
              Facebook
              <v-icon
                right
                small
                dark
              >
                mdi-facebook
              </v-icon>
            </v-btn>
          </v-list-item>

          <v-list-item>
            <v-btn
              class="ma-2 accent"
              href="https://www.linkedin.com/company/earthaid-solutions/?viewAsMember=true"
              target="_blank"
              block
            >
              Linkedin
              <v-icon
                right
                small
                dark
              >
                mdi-linkedin
              </v-icon>
            </v-btn>
          </v-list-item>
          <v-list-item>
            <v-btn
              class="ma-2 white--text secondary"
              href="https://www.betterplace.org/en/organisations/33951-earthaid-solutions-e-v"
              target="_blank"
              block
            >
              Donate

              <v-icon
                right
                small
              >
                mdi-hand-heart
              </v-icon>
            </v-btn>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </v-app-bar>
</template>

<script>
  export default {
    name: 'CoreAppBar',
    components: {
      SocialMedia: () => import('@/components/SocialMedia'),
    },
    data: () => ({
      showLogo: false,
      isScrolling: false,
      drawer: false,
      group: null,
    }),
    watch: {
      group () {
        this.drawer = false
      },
    },

    methods: {
      onScroll () {
        const offset = window.pageYOffset
        this.isScrolling = offset > 50
        this.showLogo = offset > 200
      },
    },
  }
</script>
